.map-header {
  background-color: #1b1b1b;
  width: 100%;
  position: fixed;
  z-index: 1;
}

.mapBackground1 {
  background-image: url(/public/backgrounds/mapBackground.png);
  height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.div-userLevel-explain {
  text-align: center;
  font-family: "NanumGothic";
  font-size: 3.75rem;
  font-weight: 600;
  padding-top: 10%;
  margin-bottom: 6%;
  color: #fff;
  text-shadow: 0 0 7px #fff, 0 0 42px #0fa;
}

.user-level {
  padding-left: 0;
  margin: 0 auto;
  width: 63%;
  list-style: none;
}

.user-level li {
  width: 100%;
  padding-bottom: 5%;
  display: table;
  color: white;
}

.user-level a {
  display: block;
  width: 50%;
  padding: 11%;
  border: 2px solid white;
  border-radius: 20px;

  font-weight: bold;
  font-family: "NanumGothic";
  font-size: 1.25rem;
  line-height: 1.25rem;
  text-align: center;
  color: #fff;
  text-decoration: none;
  background-color: dodgerblue;
}

#a1 {
  background-color: #ffd750;
}
#a2 {
  background-color: #4d93fc;
}
#a3 {
  background-color: #ff4a4a;
}
#a4 {
  background-color: #553830;
}
#a5 {
  background-color: #3c3c3c;
}

.user-level a:hover {
  transform: scale(1.1);
}

.user-level div {
  font-family: "NanumGothic";
  font-size: 1.25rem;
  width: 80%;
  height: 50%;
  display: table-cell;
  vertical-align: middle;
}

.link-guide {
  width: 50%;
  margin: 0 auto;
  padding-top: 2%;
  padding-bottom: 5%;
  text-align: center;
  font-size: 25px;
}

.link-guide a {
  color: white;
  text-decoration: none;
  font-family: "NanumGothic";
}

.link-guide a:hover {
  color: #0fa;
}

@media screen and (min-width: 1920px) {
  .user-level {
    width: 53%;
  }
  .user-level div {
    width: 75%;
  }
  .user-level a {
    width: 40%;
    padding: 9%;
    margin-left: 10%;
  }
}
